@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,700");
html, body, #root, .app, #dashboard {
  height: 100%;
  overflow: hidden;
  padding: 0;
  margin: 0; }

body {
  color: #444;
  font-size: 18px;
  line-height: 1.5em;
  font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif; }

h1 {
  font-size: 1.5em;
  font-weight: 300; }

h2 {
  font-size: 0.85em;
  font-weight: 700;
  text-transform: uppercase; }

p, li {
  font-size: 1em; }

.clearfix {
  clear: both; }



